import React from "react";
import { FaChalkboardTeacher } from "react-icons/fa";
import { connect } from "react-redux";
import {
  Auth,
  FC_CreateTrainingDraft,
  FC_DeleteTraining,
  FC_GetListOfTrainings,
  TrainingsItem,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
// import { DATE } from "../../utils/functions";
import { RequestTraining } from "../RequestTraining/RequestTraining";
import { TrainingDetails } from "../TrainingDetails/TrainingDetails";
import TrainingListItem from "./TrainingListItem";

// props for the component
interface AppProps {
  auth: Auth;
}

interface AppState {
  loading: boolean;
  alert: {
    target: "MAIN-ERROR" | "MAIN-SUCCESS" | null;
    msg: string;
  };
  selected_training_edit: string | null;
  selected_training_details: string | null;
  trainings: TrainingsItem[];
  loading_create_training_draft: boolean;
  delete_training: null | string;
}

const defaultAlert = {
  target: null,
  msg: "",
};

class _TrainingsList extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      alert: defaultAlert,
      selected_training_edit: null,
      selected_training_details: null,
      trainings: [],
      loading_create_training_draft: false,
      delete_training: null,
    };
  }

  componentDidMount(): void {
    if (!this.props.auth.stakeholder?.development_partner_id)
      return this.setState({
        loading: false,
        alert: {
          msg: "No Training institution assigned to you",
          target: "MAIN-ERROR",
        },
      });
    this.getTrainings(this.props.auth.stakeholder?.development_partner_id);
  }

  getTrainings(development_partner_id: string): void {
    this.setState({
      loading: true,
    });
    FC_GetListOfTrainings(development_partner_id, (status, data, err) => {
      this.setState({
        loading: false,
        alert: status
          ? defaultAlert
          : {
              target: "MAIN-ERROR",
              msg: err,
            },
        trainings: data,
      });

      this.clearError();
    });
  }

  selectTrainingToEdit = (id: string) => {
    this.setState({ selected_training_edit: id });
  };

  clearError = () =>
    setTimeout(() => {
      this.setState({
        alert: defaultAlert,
      });
    }, 3000);

  createTrainingDraft = () => {
    if (
      !window.confirm(
        "Are you sure you want to request for training application?"
      )
    )
      return;

    this.setState({ loading_create_training_draft: true });
    FC_CreateTrainingDraft(
      this.props.auth.stakeholder?.development_partner_id + "",
      (status, data, err) => {
        this.setState({
          loading_create_training_draft: false,
          trainings:
            status && data
              ? [data, ...this.state.trainings]
              : this.state.trainings,
          selected_training_edit:
            status && data ? data.id : this.state.selected_training_edit,
          alert: status
            ? defaultAlert
            : {
                target: "MAIN-ERROR",
                msg: `Error: ${err}`,
              },
        });
        this.clearError();
      }
    );
  };

  deleteTraining = (id: string | number) => {
    if (!window.confirm("Are you sure you want to delete the training?"))
      return;
    this.setState({
      delete_training: id.toString(),
    });
    FC_DeleteTraining(id.toString(), (status, err) => {
      this.setState({
        delete_training: null,
        trainings: status
          ? this.state.trainings.filter(
              (el) => el.id.toString() !== id.toString()
            )
          : this.state.trainings,
        alert: status
          ? {
              target: "MAIN-SUCCESS",
              msg: "Training has been deleted successfully!",
            }
          : {
              target: "MAIN-ERROR",
              msg: `Error: ${err}`,
            },
      });

      this.clearError();
    });
  };

  render() {
    if (this.state.selected_training_edit !== null) {
      return (
        <RequestTraining
          training_id={this.state.selected_training_edit}
          close={() => this.setState({ selected_training_edit: null })}
        />
      );
    }

    if (this.state.selected_training_details !== null)
      return (
        <TrainingDetails
          deleteUpdate={() => {
            this.setState({
              selected_training_details: null,
            });
            this.getTrainings(
              this.props.auth.stakeholder?.development_partner_id + ""
            );
          }}
          training_id={this.state.selected_training_details}
          close={() => this.setState({ selected_training_details: null })}
          editTraining={(id) => this.setState({ selected_training_edit: id })}
        />
      );

    return (
      <>
        {this.state.alert.target === "MAIN-ERROR" && (
          <Alert theme="danger" fixed>
            {this.state.alert.msg}
          </Alert>
        )}
        {this.state.alert.target === "MAIN-SUCCESS" && (
          <Alert theme="success" fixed>
            {this.state.alert.msg}
          </Alert>
        )}
        <div className="lg:sticky top-3 z-50 mb-4">
          <DashboardContainer>
            <div className="flex w-full items-center py-2">
              <div className="pr-4 text-4xl">
                <FaChalkboardTeacher />
              </div>
              <div className="flex-1">
                <Heading size="md">Trainings list</Heading>
                <p className="text-gray-600 text-sm">Training institution</p>
              </div>
              <div className="">
                <Button
                  size="md"
                  type="button"
                  onClick={this.createTrainingDraft.bind(
                    this,
                    "84b28f16-3f5b-464a-80b7-64bf7e2d7a62"
                  )}
                  theme="success-light"
                  loading={this.state.loading_create_training_draft}
                >
                  Create a training draft
                </Button>
              </div>
            </div>
          </DashboardContainer>
        </div>

        <div className="container mx-auto px-2">
          {this.state.loading === true && (
            <div className="mx-auto py-6 text-center">
              <div className="inline">
                <Loading />
              </div>
            </div>
          )}
          {this.state.trainings.length <= 0 && this.state.loading === false && (
            <div className="mx-auto py-8 text-center opacity-50">
              <h1 className="text-9xl text-center mx-auto">
                <FaChalkboardTeacher className="inline" />
              </h1>
              <h2 className="text-2xl font-bold">No Trainings found</h2>
              <p className="text-gray-500">
                You can add a new training request <br /> by clicking on{" "}
                <u>Requesting new training</u>
              </p>
            </div>
          )}

          {/* {this.state.trainings.length >= 1 && (
            <div className="bg-white">
              <table className="table border w-full">
                <thead>
                  <tr className="text-left">
                    <th className="p-2 border">#</th>
                    <th className="p-2 border">Title</th>
                    <th className="p-2 border">Area of intervention</th>
                    <th className="p-2 border">Start date</th>
                    <th className="p-2 border">End date</th>
                    <th className="p-2 border">Implementation Status</th>
                    <th className="p-2 border">Evaluation Status</th>
                    <th className="p-2 border w-10"></th>
                  </tr>
                </thead>
                <tbody>
                  {this.state.trainings.map((training, i) => (
                    <tr key={i}>
                      <td className="border p-2">{i + 1}</td>
                      <td className="border p-2">{training.title}</td>
                      <td className="border p-2">
                        {training.intervention_area}
                      </td>
                      <td className="border p-2">
                        {DATE(training.start_date, "DD/MM/YYYY")}
                      </td>
                      <td className="border p-2">
                        {DATE(training.end_date, "DD/MM/YYYY")}{" "}
                      </td>
                      <td className="border p-2">
                        {training.implementation_status}
                      </td>
                      <td className="border p-2">
                        {training.evaluation_status}
                      </td>
                      <td className="border p-2">
                        <Button size="sm">Open</Button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )} */}

          {this.state.trainings.map((training, i) => (
            <TrainingListItem
              key={i}
              training={training}
              viewDetails={(id) =>
                this.setState({
                  selected_training_details: id.toString(),
                })
              }
              deleteTraining={this.deleteTraining}
              update_loading={null}
              editTraining={(id) =>
                this.setState({ selected_training_edit: id.toString() })
              }
              delete_loading={this.state.delete_training}
            />
          ))}
        </div>
      </>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const TrainingsList = connect(mapStateToProps, {})(_TrainingsList);
